import service from "../../service.js";
import headers from "../../components/header_nav.vue"
export default {
	components: {
		headers
	},
	data() {
		return {
			Htitle: "申请结算",
			Hlicon: "../../static/icon/left_whitch.png",
			merchid:uni.getStorageSync("merchid"),
			number:0,
			status:1,
			type:0,
			error:'',
		}
	},
	onShow:function(){
		//可结算数据
		service.downLoad(this, service.api.service.settlement, {
			'merch_id': this.merchid
		},'GET', function(self, res) {
			if(res.code == 1){
				self.type = 1;
				self.number = res;
			}else{
				uni.showToast({
					icon: 'none',
					title: res.info
				});
				self.error = res.info;
			}
			
		});
	},
	methods:{
		submit(){
			var self = this;
			if(self.number == 0){
				uni.showToast({
					icon: 'none',
					title: '结算券数量小于1，无法申请结算'
				});
				return;
			}
			service.downLoad(self, service.api.service.potcode, {
				'merch_id': self.merchid
			},'POST', function(self, res) {
				res = res.data;
				console.log(res)
				if(res.code == 1){
					self.qrcode = res.info;
					self.status = 0;
					self.out();
				}else{
					uni.showToast({
						icon: 'none',
						title: res.info
					});
				}
			});
		},
		out(){
			uni.navigateBack({
				delta:1
			})
		},
		mistake(){
			uni.showToast({
				icon: 'none',
				title: this.error
			});
		},
	}
}
